<template>
    <v-card dark :color="listBackground">
        <div class="list-container">
            <single-announcement class="in-front" v-for="announcement in announcements" :key="announcement.id" :ann="announcement"/>
            <div class="in-back" v-if="enoughAnnouncements"> 
                <div class="placeholders-container">
                    <single-announcement v-for="n in 12" :key="`${n}-placeholder`" :ann="{}"/>
                </div>
            </div>
        </div>
    </v-card>
</template>

<script>
    import store from "@/store";
    import { mapState } from "vuex";
    
    import { FETCH_ANNOUNCMENTS } from "./vuex/actions";
    import SingleAnnouncement from "./components/SingleAnnouncement.vue";

    export default {
      components: {
        SingleAnnouncement
      },
      created: function() {
        store.dispatch(FETCH_ANNOUNCMENTS);
      },
      computed: {
        enoughAnnouncements() {
          const count = this.announcements.length;
          return count === 0 || count >= 9;
        },
        listBackground() {
          return this.$route.name === "flats" ? "#3c3c3c" : "#1e1e1e";
        },
        ...mapState({
          announcements: state => state.announcements.announcements
        })
      },
    };
</script>

<style lang="sass" scoped>
    .v-card
        height: 100%
        border-radius: 0
    .layout
        overflow-y: scroll
        height: 100%
    .list-container
        overflow-y: scroll
        padding-top: 10px
        height: 100%
        display: flex
        flex-direction: row
        flex-wrap: wrap
        justify-content: space-around
        position: relative

    .placeholders-container
        @extend .list-container
        overflow: hidden
    .in-front
        z-index: 2
    .in-back
        position: absolute
        z-index: 1
</style>
