<template>
<div class="ma-3 cont">
    <div @click="goToDetails">
        <div
        class="ann"
        >
        <div style="height: 220px; width: 100%;">
        <img v-if="dataLoaded"
          :src="ann.imgUrl"
          width="100%"
          class="ann-img"
        />
        <img v-else
          :src="randomPlaceholder"
          width="100%"
          class="ann-img"/>
        </div>
          <div class="pa-3 data">
            <div class="price">
              <span v-if="dataLoaded">{{ann.price}} zł</span>
              <span v-else class="fakePrice"/>
              <span v-if="dataLoaded">{{ann.rooms}} {{ann.rooms > 1 ? "rooms" : "room"}}</span>
              <span v-else class="fakeRooms"/>
            </div>
            <div  class="address">
              <span v-if="dataLoaded">{{ann.address}}</span>
              <span v-else class="fakeAddress"/>
              <span v-if="dataLoaded">{{ann.area}} m<sup>2</sup></span>
              <span v-else class="fakeArea"/>
            </div>
          </div>
        </div>
    </div>
        <div v-if="dataLoaded" class="triangle"></div>
        <heart v-if="dataLoaded" :favorite="ann.isFavorite" :id="ann.id" class="favorite"/>
</div>
</template>

<script>
  import store from "@/store";
  import Heart from "../../../components/Heart.vue";
  import placeholder1 from "@/assets/placeholder.png";
  import placeholder2 from "@/assets/placeholder2.png";
  import { SET_CURRENT_ANNOUNCMENT_ID } from "../vuex/mutations"

  export default {
    components: {
      Heart
    },
    computed: {
      dataLoaded() {
        return Boolean(this.ann.imgUrl);
      },
      randomPlaceholder() {
        const placeholders = [placeholder1, placeholder2];
        const number = Math.floor(Math.random() * placeholders.length);
        return placeholders[number];
      }
    },
    methods: {
      goToDetails() {
        if (this.ann.id) {
          store.commit( SET_CURRENT_ANNOUNCMENT_ID, { id: this.ann.id });
          this.$router.push(`/details/${this.ann.id}`);
        }
      }
    },
    props: {
      ann: Object
    }
  };
</script>

<style lang="sass" scoped>
    a
      text-decoration: none
    .cont
      position: relative
    .ann
      cursor: pointer
      height: 300px
      width: 330px
    .ann-img
      height: 220px
      border-top-right-radius: 20px
    .data
      height: 80px
      display: flex
      flex-direction: column
      justify-content: space-between
      background: #fff
      border-bottom-left-radius: 20px
      border-bottom-right-radius: 20px
    .price
      color: #fb7600
      font-size: 20px
      display: flex
      justify-content: space-between
      font-weight: bold
    .address
      color: #1e1e1e
      font-weight: bold
      font-size: 14px
      display: flex
      justify-content: space-between
    .favorite
      position: absolute !important
      top: 0
      left: 0
    
    $triangle-size: 40px
    .triangle
      cursor: pointer
      height: 0px
      width: 0px
      border-top: $triangle-size solid #fff
      border-left: $triangle-size solid #fff
      border-right: $triangle-size solid transparent
      border-bottom: $triangle-size solid transparent
      position: absolute
      top: 0
      left: 0

    =fakeTextLine($color, $width, $height)
      background: $color
      width: $width
      height: $height
    .fakePrice
      +fakeTextLine(#fb7600, 90px, 20px)
    .fakeRooms
      +fakeTextLine(#fb7600, 150px, 20px)
    .fakeAddress
      +fakeTextLine(#1c1c1c, 170px, 14px)
    .fakeArea
      +fakeTextLine(#1c1c1c, 80px, 14px)
</style>
